<template>
  <div class="container">
    <div v-if="quests.length">
      <table id="quest-list" class="table table-striped">
        <colgroup>
          <col style="width: 10%" />
          <col style="width: 45%" />
          <col style="width: 25%" />
          <col style="width: 20%" />
        </colgroup>
        <thead>
          <tr style="text-align:center">
            <th>번호</th>
            <th>제목</th>
            <th>날짜</th>
            <th>작성자</th>
          </tr>
        </thead>
        <tbody>
          <list-row
            v-for="(quest, index) in quests"
            :key="index"
            :no="index + 1"
            :q_no="quest.q_no"
            :q_title="quest.q_title"
            :q_time="quest.q_time"
            :id="quest.id"
          />
        </tbody>
      </table>
    </div>
    <div v-else class="text-center">질문이 없습니다.</div>
    <div class="pricing" style="text-align: center; margin-top:30px;">
      <button @click="movePage">질문하기</button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import ListRow from "@/components/include/ListRow.vue";

export default {
  components: {
    ListRow,
  },
  computed: {
    ...mapGetters(["quests"]),
  },
  created() {
    this.$store.dispatch("getQuests");
  },
  methods: {
    movePage() {
      //this.$router.push({name: 'book-create'});
      this.$router.push("/create");
    },
  },
};
</script>
<style scoped>
.table {
  width: 100%;
  margin-bottom: 1rem;
  color: #212529;
}
table {
  border-collapse: collapse;
}
table {
  display: table;
  text-indent: initial;
  border-spacing: 2px;
  border-color: grey;
}
</style>
